body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

h1 {
    font-family: 'Nanum Gothic',
        sans-serif;
}

p {
    font-family: 'Nanum Gothic',
        sans-serif;
    font-size: 23px;
}

.app {
    padding: 70px;
}

.navi {
    opacity: 1;
    background-color: #FFDC00;
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
    padding: 30px;
}

.navi:hover {

    text-align: center;
    background-color: #FFFFFF;
}
:root {
    --main-color: #7e96da;
    --main-user-color: #d05eca;
    --secondary-color: white;
    --main-text-color: #3e5869;
    --secondary-text-color: #b0c7d6;
    --send-message-form: #F5F5F5;
}



.message {
    margin: 15px 0;

}

.message-user {
    margin: 15px 0;
    text-align: end;


}

.message-bot {
    margin: 15px 0;
    text-align: start;

}



.message .message-username {
    font-size: 11px;
    color: #3e5869;
    color: var(--main-text-color);
    opacity: 0.9;
    margin-bottom: 6px;



}

.message .message-text {
    background: #7e96da;
    background: var(--main-color);
    color: white;
    color: var(--secondary-color);
    display: inline;
    padding: 4px 8px;
    border-radius: 8px;

}

.message-user .message-text {
    background: #d05eca;
    background: var(--main-user-color);
    color: white;
    color: var(--secondary-color);
    display: inline;
    padding: 4px 8px;
    border-radius: 8px;

}

.message-bot .message-text-bot {
    background: #7e96da;
    background: var(--main-color);
    color: white;
    color: var(--secondary-color);
    display: inline;
    padding: 4px 8px;
    border-radius: 8px;

}


.message-list {
    height: 400px;
    overflow: scroll;
    background: white;
    background: var(--secondary-color);
}

.message-list .join-room {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100%;
    font-size: 34px;
    font-weight: 300;
}

.send-message-form {
    background: #F5F5F5;
    background: var(--send-message-form);
    display: flex;
}

.send-message-form input {
    width: 100%;
    padding: 15px 10px;
    margin: 0;
    border-style: none;
    background: #F5F5F5;
    background: var(--send-message-form);
    font-weight: 200;

}

.send-message-form input:focus {
    outline-width: 0;
}

.send-message-form input::-webkit-input-placeholder {
    color: #3e5869;
    color: var(--main-text-color);
}

.send-message-form input::-ms-input-placeholder {
    color: #3e5869;
    color: var(--main-text-color);
}

.send-message-form input::placeholder {
    color: #3e5869;
    color: var(--main-text-color);
}

.help-text {
    position: absolute;
    top: 10px;
}



